/**
 * OAuth Callback Page
 * 
 * Handles the redirect from Google/Microsoft OAuth.
 * Exchanges auth code for tokens via Supabase Edge Function.
 */

import React, { useEffect, useState } from 'react';
import { supabase, isSupabaseConfigured } from '../supabaseClient';
import { useAuth } from '../contexts/AuthContext';
import { toast } from '../components/ui/use-toast';
import './OAuthCallback.css';

function OAuthCallback({ provider }) {
  const [status, setStatus] = useState('processing'); // processing | success | error
  const [error, setError] = useState(null);
  const { user } = useAuth();
  
  // Get search params from URL
  const searchParams = new URLSearchParams(window.location.search);

  useEffect(() => {
    handleCallback();
  }, []);

  const handleCallback = async () => {
    try {
      const code = searchParams.get('code');
      const state = searchParams.get('state');
      const errorParam = searchParams.get('error');
      
      // Check for OAuth errors
      if (errorParam) {
        throw new Error(searchParams.get('error_description') || 'OAuth authorization failed');
      }

      if (!code) {
        throw new Error('No authorization code received');
      }

      // Verify state matches
      const savedState = sessionStorage.getItem('oauth_state');
      if (state !== savedState) {
        throw new Error('Invalid state parameter - possible CSRF attack');
      }
      sessionStorage.removeItem('oauth_state');

      // Use provider from props or URL
      const oauthProvider = provider || 
        (window.location.pathname.includes('google') ? 'google' : 
         window.location.pathname.includes('microsoft') ? 'microsoft' : null);

      if (!oauthProvider) {
        throw new Error('Unknown OAuth provider');
      }

      // Exchange code for tokens via Edge Function
      if (!isSupabaseConfigured()) {
        throw new Error('Supabase not configured');
      }

      const { data, error: fnError } = await supabase.functions.invoke('oauth-exchange', {
        body: {
          code,
          provider: oauthProvider,
          redirectUri: `${window.location.origin}/auth/callback/${oauthProvider}`,
        },
      });

      if (fnError) throw fnError;
      if (data?.error) throw new Error(data.error);

      // Store connection in database
      const { error: dbError } = await supabase
        .from('user_connections')
        .upsert({
          user_id: user?.id || 'dan',
          provider: oauthProvider,
          access_token: data.access_token,
          refresh_token: data.refresh_token,
          expires_at: data.expires_at,
          email: data.email,
          connected_at: new Date().toISOString(),
        }, {
          onConflict: 'user_id,provider',
        });

      if (dbError) throw dbError;

      setStatus('success');
      toast({ title: '!', description: `${oauthProvider === 'google' ? 'Google Calendar' : 'Outlook'}  ` });

      // Redirect back to app after short delay
      setTimeout(() => {
        window.location.href = '/?mode=command';
      }, 2000);

    } catch (err) {
      console.error('OAuth callback error:', err);
      setStatus('error');
      setError(err.message);
      toast({ title: '', description: err.message, variant: 'destructive' });
    }
  };

  return (
    <div className="oauth-callback">
      <div className="oauth-callback-card">
        {status === 'processing' && (
          <>
            <div className="oauth-spinner" />
            <h2>   ...</h2>
            <p> </p>
          </>
        )}
        
        {status === 'success' && (
          <>
            <div className="oauth-success"></div>
            <h2> !</h2>
            <p> ...</p>
          </>
        )}
        
        {status === 'error' && (
          <>
            <div className="oauth-error"></div>
            <h2> </h2>
            <p>{error}</p>
            <button 
              className="oauth-retry"
              onClick={() => window.location.href = '/'}
            >
               
            </button>
          </>
        )}
      </div>
    </div>
  );
}

export default OAuthCallback;
