import React, { useState, useEffect } from 'react';
import './App.css';
import CommandMode from './modes/CommandMode';
import TransparencyMode from './modes/TransparencyMode';
import WorkbenchMode from './modes/WorkbenchMode';
import HorizonMode from './modes/HorizonMode';
import PAInbox from './components/PAInbox';
import DayClose from './components/DayClose';
import QuickAdd from './components/QuickAdd';
import GoldenDemo from './pages/GoldenDemo';
import AuthPage from './pages/AuthPage';
import SettingsPage from './pages/SettingsPage';
import OAuthCallback from './pages/OAuthCallback';
import { AuthProvider, useAuth } from './contexts/AuthContext';
import { Toaster } from './components/ui/toaster';

// Check if we're in dev mode
const isDev = process.env.NODE_ENV === 'development' || window.location.hostname === 'localhost';

const MODES = {
  command: { label: '', icon: '' },
  transparency: { label: '', icon: '' },
  workbench: { label: '', icon: '' },
  horizon: { label: '', icon: '' },
  'pa-inbox': { label: ' PA', icon: '' },
  // Dev-only modes
  ...(isDev && { 'golden-demo': { label: 'Golden', icon: '' } }),
};

// Get current hour in Asia/Nicosia timezone
function getNicosiaHour() {
  return new Date(new Date().toLocaleString('en-US', { timeZone: 'Asia/Nicosia' })).getHours();
}

// Feature flag: Set to true to enable auth gate
const AUTH_ENABLED = process.env.REACT_APP_AUTH_ENABLED === 'true';

function AppContent() {
  const { user, loading: authLoading } = useAuth();
  const [mode, setMode] = useState(() => {
    const params = new URLSearchParams(window.location.search);
    return params.get('mode') || 'command';
  });
  const [showDayClose, setShowDayClose] = useState(false);
  const [dayCloseDismissed, setDayCloseDismissed] = useState(false);
  const [showSettings, setShowSettings] = useState(false);
  const [isDark, setIsDark] = useState(() => {
    const saved = localStorage.getItem('theme');
    return saved === 'dark';
  });

  // Apply theme to document
  useEffect(() => {
    if (isDark) {
      document.documentElement.classList.add('dark');
    } else {
      document.documentElement.classList.remove('dark');
    }
    localStorage.setItem('theme', isDark ? 'dark' : 'light');
  }, [isDark]);

  // Auto-show DayClose after 21:00 (once per session)
  useEffect(() => {
    const checkDayClose = () => {
      const hour = getNicosiaHour();
      if (hour >= 21 && !dayCloseDismissed && !showDayClose) {
        setShowDayClose(true);
      }
    };
    checkDayClose();
    const interval = setInterval(checkDayClose, 60000); // Check every minute
    return () => clearInterval(interval);
  }, [dayCloseDismissed, showDayClose]);

  useEffect(() => {
    const handlePop = () => {
      const params = new URLSearchParams(window.location.search);
      setMode(params.get('mode') || 'command');
    };
    window.addEventListener('popstate', handlePop);
    return () => window.removeEventListener('popstate', handlePop);
  }, []);

  const handleModeChange = (newMode) => {
    setMode(newMode);
    const url = new URL(window.location);
    if (newMode === 'command') {
      url.searchParams.delete('mode');
    } else {
      url.searchParams.set('mode', newMode);
    }
    window.history.pushState({}, '', url);
  };

  // Handle OAuth callbacks
  const path = window.location.pathname;
  if (path.includes('/auth/callback/')) {
    const provider = path.includes('google') ? 'google' : 
                     path.includes('microsoft') ? 'microsoft' : null;
    return <OAuthCallback provider={provider} />;
  }

  // Show auth page if auth is enabled and user is not logged in
  if (AUTH_ENABLED && !authLoading && !user) {
    return <AuthPage onSuccess={() => window.location.reload()} />;
  }

  // Show loading while checking auth
  if (AUTH_ENABLED && authLoading) {
    return (
      <div className="app" style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', minHeight: '100vh' }}>
        <div className="cmd-pulse" />
      </div>
    );
  }

  const handleDayCloseClose = () => {
    setShowDayClose(false);
    setDayCloseDismissed(true);
  };

  const renderMode = () => {
    switch (mode) {
      case 'transparency':
        return <TransparencyMode />;
      case 'workbench':
        return <WorkbenchMode />;
      case 'horizon':
        return <HorizonMode />;
      case 'pa-inbox':
        return <PAInbox />;
      case 'golden-demo':
        return <GoldenDemo />;
      case 'command':
      default:
        return <CommandMode />;
    }
  };

  return (
    <div className="app">
      {renderMode()}

      {/* Mode Switcher  bottom pill */}
      <nav className="mode-switcher">
        {Object.entries(MODES).map(([key, { label, icon }]) => (
          <button
            key={key}
            className={`mode-btn ${mode === key ? 'mode-active' : ''}`}
            onClick={() => handleModeChange(key)}
          >
            <span className="mode-icon">{icon}</span>
            <span className="mode-label">{label}</span>
          </button>
        ))}
      </nav>

      {/* Settings Button */}
      <button
        className="settings-trigger"
        onClick={() => setShowSettings(true)}
        title=""
      >
        
      </button>

      {/* Theme Toggle */}
      <button
        className="theme-toggle"
        onClick={() => setIsDark(!isDark)}
        title={isDark ? 'Switch to Light Mode' : 'Switch to Dark Mode'}
      >
        {isDark ? '' : ''}
      </button>

      {/* Day Close button (visible after 21:00) */}
      {getNicosiaHour() >= 21 && !showDayClose && (
        <button
          className="day-close-trigger"
          onClick={() => setShowDayClose(true)}
          title="Day Close"
        >
          
        </button>
      )}

      {/* Day Close Modal */}
      {showDayClose && <DayClose onClose={handleDayCloseClose} />}

      {/* Settings Modal */}
      {showSettings && <SettingsPage onClose={() => setShowSettings(false)} />}

      {/* Quick Add FAB */}
      <QuickAdd />
      
      {/* Global Toast Notifications */}
      <Toaster />
    </div>
  );
}

// Main App with AuthProvider
function App() {
  return (
    <AuthProvider>
      <AppContent />
    </AuthProvider>
  );
}

export default App;
