/**
 * Auth Page
 * 
 * Clean, minimal login/signup form.
 * Follows the calm design philosophy  no distractions.
 */

import React, { useState } from 'react';
import { useAuth } from '../contexts/AuthContext';
import { supabase, isSupabaseConfigured } from '../supabaseClient';
import { toast } from '../components/ui/use-toast';
import './AuthPage.css';

function AuthPage({ onSuccess }) {
  const [mode, setMode] = useState('signin'); // 'signin' | 'signup' | 'reset'
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [name, setName] = useState('');
  const [loading, setLoading] = useState(false);
  const [socialLoading, setSocialLoading] = useState(null);
  
  const { signIn, signUp, resetPassword } = useAuth();

  // Social Login with Google
  const handleGoogleLogin = async () => {
    if (!isSupabaseConfigured()) {
      toast({ title: '', description: 'Supabase  ', variant: 'destructive' });
      return;
    }
    setSocialLoading('google');
    try {
      const { error } = await supabase.auth.signInWithOAuth({
        provider: 'google',
        options: {
          redirectTo: window.location.origin,
          scopes: 'https://www.googleapis.com/auth/calendar.readonly',
        },
      });
      if (error) throw error;
    } catch (error) {
      console.error('Google login error:', error);
      toast({ title: '', description: error.message, variant: 'destructive' });
      setSocialLoading(null);
    }
  };

  // Social Login with Microsoft
  const handleMicrosoftLogin = async () => {
    if (!isSupabaseConfigured()) {
      toast({ title: '', description: 'Supabase  ', variant: 'destructive' });
      return;
    }
    setSocialLoading('microsoft');
    try {
      const { error } = await supabase.auth.signInWithOAuth({
        provider: 'azure',
        options: {
          redirectTo: window.location.origin,
          scopes: 'Calendars.Read',
        },
      });
      if (error) throw error;
    } catch (error) {
      console.error('Microsoft login error:', error);
      toast({ title: '', description: error.message, variant: 'destructive' });
      setSocialLoading(null);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);

    try {
      if (mode === 'signin') {
        const { error } = await signIn(email, password);
        if (error) throw error;
        toast({ title: '! ', description: ' ' });
        onSuccess?.();
      } else if (mode === 'signup') {
        const { error } = await signUp(email, password, { name });
        if (error) throw error;
        toast({ title: ' !', description: '    ' });
        setMode('signin');
      } else if (mode === 'reset') {
        const { error } = await resetPassword(email);
        if (error) throw error;
        toast({ title: '!', description: '    ' });
        setMode('signin');
      }
    } catch (error) {
      console.error('Auth error:', error);
      toast({ 
        title: '', 
        description: error.message || ' ,  ',
        variant: 'destructive' 
      });
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="auth-page">
      <div className="auth-container">
        {/* Logo/Brand */}
        <div className="auth-header">
          <h1 className="auth-logo">Life OS</h1>
          <p className="auth-tagline">   </p>
        </div>

        {/* Social Login Buttons */}
        <div className="auth-social">
          <button
            type="button"
            className="auth-social-btn auth-google"
            onClick={handleGoogleLogin}
            disabled={socialLoading !== null}
          >
            <svg viewBox="0 0 24 24" width="20" height="20">
              <path fill="#4285F4" d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z"/>
              <path fill="#34A853" d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z"/>
              <path fill="#FBBC05" d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z"/>
              <path fill="#EA4335" d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z"/>
            </svg>
            {socialLoading === 'google' ? '...' : '  Google'}
          </button>
          
          <button
            type="button"
            className="auth-social-btn auth-microsoft"
            onClick={handleMicrosoftLogin}
            disabled={socialLoading !== null}
          >
            <svg viewBox="0 0 24 24" width="20" height="20">
              <path fill="#F25022" d="M1 1h10v10H1z"/>
              <path fill="#00A4EF" d="M1 13h10v10H1z"/>
              <path fill="#7FBA00" d="M13 1h10v10H13z"/>
              <path fill="#FFB900" d="M13 13h10v10H13z"/>
            </svg>
            {socialLoading === 'microsoft' ? '...' : '  Microsoft'}
          </button>
        </div>

        <div className="auth-divider">
          <span></span>
        </div>

        {/* Form */}
        <form className="auth-form" onSubmit={handleSubmit}>
          {mode === 'signup' && (
            <div className="auth-field">
              <label htmlFor="name"></label>
              <input
                id="name"
                type="text"
                value={name}
                onChange={(e) => setName(e.target.value)}
                placeholder="  ?"
                required={mode === 'signup'}
                disabled={loading}
              />
            </div>
          )}

          <div className="auth-field">
            <label htmlFor="email"></label>
            <input
              id="email"
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              placeholder="you@example.com"
              required
              disabled={loading}
              autoComplete="email"
            />
          </div>

          {mode !== 'reset' && (
            <div className="auth-field">
              <label htmlFor="password"></label>
              <input
                id="password"
                type="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                placeholder=""
                required={mode !== 'reset'}
                disabled={loading}
                autoComplete={mode === 'signin' ? 'current-password' : 'new-password'}
                minLength={6}
              />
            </div>
          )}

          <button
            type="submit"
            className="auth-submit"
            disabled={loading}
          >
            {loading ? '...' : (
              mode === 'signin' ? '' :
              mode === 'signup' ? ' ' :
              '  '
            )}
          </button>
        </form>

        {/* Mode Switcher */}
        <div className="auth-switch">
          {mode === 'signin' && (
            <>
              <button 
                type="button"
                className="auth-link"
                onClick={() => setMode('signup')}
              >
                  ? 
              </button>
              <button 
                type="button"
                className="auth-link"
                onClick={() => setMode('reset')}
              >
                 ?
              </button>
            </>
          )}
          {mode === 'signup' && (
            <button 
              type="button"
              className="auth-link"
              onClick={() => setMode('signin')}
            >
                ? 
            </button>
          )}
          {mode === 'reset' && (
            <button 
              type="button"
              className="auth-link"
              onClick={() => setMode('signin')}
            >
               
            </button>
          )}
        </div>
      </div>
    </div>
  );
}

export default AuthPage;
