/**
 * QuickAdd Component
 * 
 * Floating Action Button + Quick capture modal
 * Available from any screen for fast task entry
 * 
 * Philosophy:
 * - Minimal friction
 * - Smart defaults
 * - One tap to add
 */

import React, { useState, useEffect, useRef } from 'react';
import { supabase, isSupabaseConfigured } from '../supabaseClient';
import { toast } from './ui/use-toast';
import './QuickAdd.css';

// Context options for quick assignment
const CONTEXTS = [
  { id: 'work', label: 'XBO', icon: '' },
  { id: 'salon', label: 'La Luce', icon: '' },
  { id: 'realestate', label: 'DSP', icon: '' },
  { id: 'tlm', label: 'TLM', icon: '' },
  { id: 'personal', label: '', icon: '' },
  { id: 'family', label: '', icon: '' },
  { id: 'sport', label: '', icon: '' },
];

function QuickAdd() {
  const [isOpen, setIsOpen] = useState(false);
  const [text, setText] = useState('');
  const [area, setArea] = useState(null);
  const [saving, setSaving] = useState(false);
  const inputRef = useRef(null);

  // Focus input when opened
  useEffect(() => {
    if (isOpen && inputRef.current) {
      setTimeout(() => inputRef.current?.focus(), 100);
    }
  }, [isOpen]);

  // Keyboard shortcuts
  useEffect(() => {
    const handleKeyDown = (e) => {
      // Cmd/Ctrl + K to open
      if ((e.metaKey || e.ctrlKey) && e.key === 'k') {
        e.preventDefault();
        setIsOpen(true);
      }
      // Escape to close
      if (e.key === 'Escape' && isOpen) {
        handleClose();
      }
    };
    document.addEventListener('keydown', handleKeyDown);
    return () => document.removeEventListener('keydown', handleKeyDown);
  }, [isOpen]);

  const handleClose = () => {
    setIsOpen(false);
    setText('');
    setArea(null);
  };

  const handleSubmit = async (e) => {
    e?.preventDefault();
    
    if (!text.trim()) {
      inputRef.current?.focus();
      return;
    }

    setSaving(true);

    try {
      if (isSupabaseConfigured()) {
        const { error } = await supabase.from('tasks').insert({
          id: 'task-' + Date.now(),
          user_id: 'dan',
          text: text.trim(),
          area: area,
          labels: [],
          energy_level: 'medium',
          assignee: 'dan',
          completed: false,
          archived: false,
          created_at: new Date().toISOString(),
        });

        if (error) throw error;
      }

      toast({ title: ' ', description: text.slice(0, 30) + (text.length > 30 ? '...' : '') });
      handleClose();
    } catch (err) {
      console.error('Quick add error:', err);
      toast({ title: '', description: '  ', variant: 'destructive' });
    } finally {
      setSaving(false);
    }
  };

  return (
    <>
      {/* FAB Button */}
      <button
        className="quick-add-fab"
        onClick={() => setIsOpen(true)}
        aria-label=" "
      >
        <span className="quick-add-fab-icon">+</span>
      </button>

      {/* Modal Overlay */}
      {isOpen && (
        <div className="quick-add-overlay" onClick={handleClose}>
          <div className="quick-add-modal" onClick={e => e.stopPropagation()}>
            <form onSubmit={handleSubmit}>
              {/* Input */}
              <input
                ref={inputRef}
                type="text"
                className="quick-add-input"
                placeholder="  ?"
                value={text}
                onChange={e => setText(e.target.value)}
                disabled={saving}
                autoComplete="off"
              />

              {/* Context Pills */}
              <div className="quick-add-contexts">
                {CONTEXTS.map(ctx => (
                  <button
                    key={ctx.id}
                    type="button"
                    className={`quick-add-context ${area === ctx.id ? 'active' : ''}`}
                    onClick={() => setArea(area === ctx.id ? null : ctx.id)}
                  >
                    <span>{ctx.icon}</span>
                    <span>{ctx.label}</span>
                  </button>
                ))}
              </div>

              {/* Actions */}
              <div className="quick-add-actions">
                <span className="quick-add-hint">
                  K  
                </span>
                <div className="quick-add-buttons">
                  <button
                    type="button"
                    className="quick-add-cancel"
                    onClick={handleClose}
                    disabled={saving}
                  >
                    
                  </button>
                  <button
                    type="submit"
                    className="quick-add-submit"
                    disabled={saving || !text.trim()}
                  >
                    {saving ? '...' : ''}
                  </button>
                </div>
              </div>
            </form>
          </div>
        </div>
      )}
    </>
  );
}

export default QuickAdd;
