/**
 * Settings Page
 * 
 * User preferences and integrations.
 * Includes OAuth connections for Google Calendar and Outlook.
 * Golden design with premium feel.
 */

import React, { useState, useEffect } from 'react';
import { supabase, isSupabaseConfigured } from '../supabaseClient';
import { useAuth } from '../contexts/AuthContext';
import { toast } from '../components/ui/use-toast';
import './SettingsPage.css';

// OAuth URLs - these would be set up in your Google/Microsoft apps
const GOOGLE_CLIENT_ID = process.env.REACT_APP_GOOGLE_CLIENT_ID;
const MICROSOFT_CLIENT_ID = process.env.REACT_APP_MICROSOFT_CLIENT_ID;

// Check if OAuth is configured
const isGoogleConfigured = !!GOOGLE_CLIENT_ID;
const isMicrosoftConfigured = !!MICROSOFT_CLIENT_ID;

function SettingsPage({ onClose }) {
  const { user, signOut } = useAuth();
  const [connections, setConnections] = useState({
    google: false,
    microsoft: false,
  });
  const [loading, setLoading] = useState(true);
  const [syncStatus, setSyncStatus] = useState({
    google: null,
    microsoft: null,
  });

  useEffect(() => {
    checkConnections();
  }, []);

  const checkConnections = async () => {
    if (!isSupabaseConfigured() || !user) {
      setLoading(false);
      return;
    }

    try {
      const { data } = await supabase
        .from('user_connections')
        .select('provider, last_sync, sync_status')
        .eq('user_id', user.id);

      if (data) {
        setConnections({
          google: data.some(c => c.provider === 'google'),
          microsoft: data.some(c => c.provider === 'microsoft'),
        });
        
        // Get sync status
        const googleConn = data.find(c => c.provider === 'google');
        const msConn = data.find(c => c.provider === 'microsoft');
        setSyncStatus({
          google: googleConn ? { lastSync: googleConn.last_sync, status: googleConn.sync_status } : null,
          microsoft: msConn ? { lastSync: msConn.last_sync, status: msConn.sync_status } : null,
        });
      }
    } catch (err) {
      console.error('Error checking connections:', err);
    }
    setLoading(false);
  };

  const handleGoogleConnect = () => {
    if (!GOOGLE_CLIENT_ID) {
      toast({ 
        title: ' ', 
        description: ' Google Calendar   .  !',
      });
      return;
    }

    // Build OAuth URL
    const redirectUri = `${window.location.origin}/auth/callback/google`;
    const scope = encodeURIComponent('https://www.googleapis.com/auth/calendar.readonly');
    const state = crypto.randomUUID();
    
    // Store state for verification
    sessionStorage.setItem('oauth_state', state);
    
    const authUrl = `https://accounts.google.com/o/oauth2/v2/auth?` +
      `client_id=${GOOGLE_CLIENT_ID}` +
      `&redirect_uri=${encodeURIComponent(redirectUri)}` +
      `&response_type=code` +
      `&scope=${scope}` +
      `&state=${state}` +
      `&access_type=offline` +
      `&prompt=consent`;

    window.location.href = authUrl;
  };

  const handleMicrosoftConnect = () => {
    if (!MICROSOFT_CLIENT_ID) {
      toast({ 
        title: ' ', 
        description: ' Outlook   .  !',
      });
      return;
    }

    const redirectUri = `${window.location.origin}/auth/callback/microsoft`;
    const scope = encodeURIComponent('Calendars.Read offline_access');
    const state = crypto.randomUUID();
    
    sessionStorage.setItem('oauth_state', state);
    
    const authUrl = `https://login.microsoftonline.com/common/oauth2/v2.0/authorize?` +
      `client_id=${MICROSOFT_CLIENT_ID}` +
      `&redirect_uri=${encodeURIComponent(redirectUri)}` +
      `&response_type=code` +
      `&scope=${scope}` +
      `&state=${state}`;

    window.location.href = authUrl;
  };

  const handleDisconnect = async (provider) => {
    if (!isSupabaseConfigured() || !user) return;

    try {
      const { error } = await supabase
        .from('user_connections')
        .delete()
        .eq('user_id', user.id)
        .eq('provider', provider);

      if (error) throw error;

      setConnections(prev => ({ ...prev, [provider]: false }));
      setSyncStatus(prev => ({ ...prev, [provider]: null }));
      toast({ title: ' ', description: ` ${provider === 'google' ? 'Google' : 'Microsoft'}  ` });
    } catch (err) {
      console.error('Disconnect error:', err);
      toast({ title: '', description: '  ', variant: 'destructive' });
    }
  };

  const handleSignOut = async () => {
    await signOut();
    window.location.reload();
  };

  const formatLastSync = (dateStr) => {
    if (!dateStr) return null;
    const date = new Date(dateStr);
    const now = new Date();
    const diffMinutes = Math.floor((now - date) / 60000);
    
    if (diffMinutes < 1) return '';
    if (diffMinutes < 60) return ` ${diffMinutes} `;
    if (diffMinutes < 1440) return ` ${Math.floor(diffMinutes / 60)} `;
    return ` ${Math.floor(diffMinutes / 1440)} `;
  };

  return (
    <div className="settings-overlay" onClick={onClose}>
      <div className="settings-modal" onClick={e => e.stopPropagation()}>
        <header className="settings-header">
          <div className="settings-header-content">
            <h2> </h2>
            <span className="settings-header-sub">  </span>
          </div>
          <button className="settings-close" onClick={onClose}></button>
        </header>

        <div className="settings-content">
          {/* Calendar Integrations */}
          <section className="settings-section">
            <div className="settings-section-header">
              <h3>  </h3>
              <span className="settings-section-badge"> </span>
            </div>
            <p className="settings-desc">
                       -Life OS
            </p>

            <div className="settings-integrations">
              {/* Google Calendar */}
              <div className={`settings-integration ${connections.google ? 'settings-integration-connected' : ''} ${!isGoogleConfigured && !connections.google ? 'settings-integration-pending' : ''}`}>
                <div className="settings-integration-info">
                  <span className="settings-integration-icon">
                    <svg viewBox="0 0 24 24" width="24" height="24">
                      <path fill="#4285F4" d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z"/>
                      <path fill="#34A853" d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z"/>
                      <path fill="#FBBC05" d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z"/>
                      <path fill="#EA4335" d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z"/>
                    </svg>
                  </span>
                  <div className="settings-integration-text">
                    <span className="settings-integration-name">Google Calendar</span>
                    {connections.google ? (
                      <span className="settings-integration-status connected">
                        <span className="settings-status-dot"></span>
                         
                        {syncStatus.google?.lastSync && (
                          <span className="settings-last-sync">
                              {formatLastSync(syncStatus.google.lastSync)}
                          </span>
                        )}
                      </span>
                    ) : !isGoogleConfigured ? (
                      <span className="settings-integration-status pending">
                        <span className="settings-status-dot pending"></span>
                          
                      </span>
                    ) : (
                      <span className="settings-integration-status">
                         
                      </span>
                    )}
                  </div>
                </div>
                {connections.google ? (
                  <button 
                    className="settings-btn settings-btn-disconnect"
                    onClick={() => handleDisconnect('google')}
                  >
                    
                  </button>
                ) : (
                  <button 
                    className={`settings-btn ${isGoogleConfigured ? 'settings-btn-connect' : 'settings-btn-pending'}`}
                    onClick={handleGoogleConnect}
                    disabled={loading}
                  >
                    {isGoogleConfigured ? '' : ' '}
                  </button>
                )}
              </div>

              {/* Microsoft Outlook */}
              <div className={`settings-integration ${connections.microsoft ? 'settings-integration-connected' : ''} ${!isMicrosoftConfigured && !connections.microsoft ? 'settings-integration-pending' : ''}`}>
                <div className="settings-integration-info">
                  <span className="settings-integration-icon">
                    <svg viewBox="0 0 24 24" width="24" height="24">
                      <path fill="#0078D4" d="M21.17 2H7.83A1.83 1.83 0 0 0 6 3.83v3.51l8 3.07 8-3.07V3.83A1.83 1.83 0 0 0 21.17 2z"/>
                      <path fill="#0364B8" d="M22 8.61l-8 3.07-8-3.07v8.56A1.83 1.83 0 0 0 7.83 19h13.34A1.83 1.83 0 0 0 23 17.17V8.61z"/>
                      <path fill="#28A8EA" d="M6 7.34v11.49l-4.83-2.9A1 1 0 0 1 .67 15V6.9a1 1 0 0 1 1.5-.87L6 7.34z"/>
                    </svg>
                  </span>
                  <div className="settings-integration-text">
                    <span className="settings-integration-name">Outlook Calendar</span>
                    {connections.microsoft ? (
                      <span className="settings-integration-status connected">
                        <span className="settings-status-dot"></span>
                         
                        {syncStatus.microsoft?.lastSync && (
                          <span className="settings-last-sync">
                              {formatLastSync(syncStatus.microsoft.lastSync)}
                          </span>
                        )}
                      </span>
                    ) : !isMicrosoftConfigured ? (
                      <span className="settings-integration-status pending">
                        <span className="settings-status-dot pending"></span>
                          
                      </span>
                    ) : (
                      <span className="settings-integration-status">
                         
                      </span>
                    )}
                  </div>
                </div>
                {connections.microsoft ? (
                  <button 
                    className="settings-btn settings-btn-disconnect"
                    onClick={() => handleDisconnect('microsoft')}
                  >
                    
                  </button>
                ) : (
                  <button 
                    className={`settings-btn ${isMicrosoftConfigured ? 'settings-btn-connect' : 'settings-btn-pending'}`}
                    onClick={handleMicrosoftConnect}
                    disabled={loading}
                  >
                    {isMicrosoftConfigured ? '' : ' '}
                  </button>
                )}
              </div>
            </div>

            {/* Info Banner for pending integrations */}
            {(!isGoogleConfigured || !isMicrosoftConfigured) && (
              <div className="settings-info-banner">
                <span className="settings-info-icon"></span>
                <p>
                    !   ,      .
                </p>
              </div>
            )}
          </section>

          {/* Preferences Section */}
          <section className="settings-section">
            <div className="settings-section-header">
              <h3>  </h3>
            </div>
            <div className="settings-preferences">
              <div className="settings-preference-item">
                <div className="settings-preference-info">
                  <span className="settings-preference-label"> </span>
                  <span className="settings-preference-desc"> / </span>
                </div>
                <span className="settings-preference-value">
                    
                </span>
              </div>
              <div className="settings-preference-item">
                <div className="settings-preference-info">
                  <span className="settings-preference-label"> </span>
                  <span className="settings-preference-desc">  </span>
                </div>
                <span className="settings-preference-value">RTL </span>
              </div>
            </div>
          </section>

          {/* Account */}
          {user && (
            <section className="settings-section">
              <div className="settings-section-header">
                <h3> </h3>
              </div>
              <div className="settings-account">
                <div className="settings-account-info">
                  <span className="settings-account-avatar">
                    {user.email?.[0]?.toUpperCase() || 'U'}
                  </span>
                  <div className="settings-account-details">
                    <span className="settings-account-name">
                      {user.user_metadata?.full_name || user.email?.split('@')[0]}
                    </span>
                    <span className="settings-email">{user.email}</span>
                  </div>
                </div>
                <button 
                  className="settings-btn settings-btn-danger"
                  onClick={handleSignOut}
                >
                  
                </button>
              </div>
            </section>
          )}

          {/* Footer */}
          <footer className="settings-footer">
            <span className="settings-version">Life OS v0.1.0</span>
            <span className="settings-built">   </span>
          </footer>
        </div>
      </div>
    </div>
  );
}

export default SettingsPage;
