/**
 * Skeleton Loading Component
 * 
 * Provides loading placeholders that match content shape.
 * Used during data fetching and mode transitions.
 */

import React from 'react';
import './Skeleton.css';

export function Skeleton({ width, height, className = '', variant = 'default' }) {
  const style = {
    width: width || '100%',
    height: height || '1em',
  };

  return (
    <div 
      className={`skeleton skeleton-${variant} ${className}`}
      style={style}
      aria-hidden="true"
    />
  );
}

export function SkeletonCard({ lines = 3 }) {
  return (
    <div className="skeleton-card">
      <div className="skeleton-card-header">
        <Skeleton width="2rem" height="2rem" variant="circle" />
        <Skeleton width="40%" height="0.75rem" />
      </div>
      <div className="skeleton-card-body">
        {Array.from({ length: lines }).map((_, i) => (
          <Skeleton 
            key={i} 
            width={i === lines - 1 ? '60%' : '100%'} 
            height="0.875rem" 
          />
        ))}
      </div>
    </div>
  );
}

export function SkeletonList({ count = 5 }) {
  return (
    <div className="skeleton-list">
      {Array.from({ length: count }).map((_, i) => (
        <div key={i} className="skeleton-list-item">
          <Skeleton width="1.25rem" height="1.25rem" variant="circle" />
          <Skeleton width={`${60 + Math.random() * 30}%`} height="1rem" />
        </div>
      ))}
    </div>
  );
}

export function CommandSkeleton() {
  return (
    <div className="cmd-skeleton">
      <div className="cmd-skeleton-header">
        <Skeleton width="180px" height="2rem" />
        <Skeleton width="120px" height="1rem" />
        <Skeleton width="80px" height="1.5rem" variant="pill" />
      </div>
      <div className="cmd-skeleton-cards">
        <SkeletonCard lines={2} />
        <SkeletonCard lines={3} />
        <SkeletonCard lines={2} />
      </div>
    </div>
  );
}

export default Skeleton;
